*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.app{
    min-height: 100vh;
    font: normal 16px Roboto, Arial, Helvetica, sans-serif;
    background: #a7a7a7;
    background: #FF7F00;
    background: -webkit-linear-gradient(#FF7F00, #ffba00);
    background: linear-gradient(#FF7F00, #ffba00);
}

.app__wrapper{
    max-width: 600px;
    padding: 20px 0;
    margin: 0 auto;
}

.app__main{
    padding: 15px 50px 30px 50px;
}

@media (max-width: 550px) {
    .app__main{
        padding: 15px 0 25px 0;
    }
}

.error-message{
    padding: 10px;
    white-space: pre-line;
    text-align: center;
    background: #F44336;
    color: #FFF;
}

.header__title{
    text-align: center;
    font-size: 40px;
    font-weight: 700;
    color: #FFF;
}

@media (max-width: 550px) {
    .header__title{
        padding-left: 40px;
        font-size: 35px;
        text-align: left;
    }
}

.weather-card{
    position: relative;
    margin-bottom: 15px;
    padding: 20px 30px;
    font-size: 17px;
    background: #FFF3E4;
    color: #515151;
}

.weather-card__close{
    position: absolute;
    top: 7px;
    right: 25px;
    font-size: 30px;
    font-family: monospace;
    color: #FF860E;
    cursor: pointer;
}

.weather-card__body{
    padding: 0 20px 8px 20px;
    margin-bottom: 10px;
    border-bottom: 1px solid #FF9A00;
}

.weather-card__city{
    font-weight: 600;
}

.weather-card__temperature{
    margin: 14px 0 8px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 45px;
    font-weight: 600;
}

.weather-card__now{
    font-weight: 100;
}

.weather-card__now b{
    font-weight: 600;
}

.weather-card__now td{
    padding: 9px 0;
}

.weather-card__now img{
    margin-right: 2px;
}

.weather-card__now__first-column{
    width: 125px;
}

.weather-card__footer{
    text-align: center;
    padding: 0 5px;
    width: 100%;
    font-weight: 900;
}

.weather-card__weekdays__temp{
    padding-top: 4px;
    display: inline-block;
    color: #FF9A00;
}

@media (max-width: 550px) {
    .weather-card{
        padding: 17px 20px;
        font-size: 15px;
    }

    .weather-card__close{
        right: 15px;
    }

    .weather-card__body{
        padding: 0 3px 8px 3px;
    }

    .weather-card__temperature{
        font-size: 35px;
    }

    .weather-card__now td{
        padding: 5px 0;
    }

    .weather-card__footer{
        padding-right: 0;
        padding-left: 0;
    }

    .weather-card__weekday{
        display: none;
    }

    .weather-card__weekday[data-order="0"],
    .weather-card__weekday[data-order="1"],
    .weather-card__weekday[data-order="2"],
    .weather-card__weekday[data-order="3"]{
        display: table-cell;
    }
}
.search-bar__form{
    position: relative;
}

.search-bar__input{
    padding: 14px 55px 14px 25px;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: 1px solid #D86C00;
    color: #313131;
}

.search-bar__input::-webkit-input-placeholder{ font-weight: 300; color: #919191; }
.search-bar__input::-ms-input-placeholder{ font-weight: 300; color: #919191; }
.search-bar__input::placeholder{ font-weight: 300; color: #919191; }

.search-bar__submit{
    position: absolute;
    top: 1px;
    bottom: 1px;
    right: 1px;
    padding: 0 13px;
    border: none;
    background: #FFF;
    cursor: pointer;
    opacity: 0.7;
}

.search-bar__submit:hover,
.search-bar__submit:focus{
    opacity: 1;
}

@media (max-width: 550px) {
    .search-bar{
        padding-left: 20px;
        padding-right: 20px;
    }

    .search-bar__input{
        padding: 10px 47px 10px 20px;
    }
}

.footer{
    padding: 25px 70px 0 100px;
    border-top: 1px solid #FFF;
}

.footer__title{
    margin-bottom: 5px;
    font-size: 30px;
    font-weight: 700;
    text-align: left;
    color: #FFF;
}

.foooter__capitals{
    text-align: left;
}

@media (max-width: 550px) {
    .footer{
        padding-top: 15px;
        padding-right: 40px;
        padding-left: 40px;
    }
}

.capitals__column-wrapper{
    display: inline-block;
    width: 50%;
    vertical-align: top;
}

.capitals__table{
    width: 100%;
    color: #202020;
}

.capitals__table th,
.capitals__table td{
    padding: 9px 0;
}

.capitals__table th{
    font-size: 14px;
    font-weight: 300;
    text-align: left;
    color: #222;
}

.capitals-column__temperature{
    width: 35px;
}

.capitals__table td{
    font-weight: 700;
}

.capitals__colMin,
.capitals__colMax{
    width: 30px;
}

@media (max-width: 550px) {
    .capitals__column-wrapper{
        width: 100%;
    }

    .capitals__column-wrapper[data-order="2"] thead{
        display: none;
    }

    .capitals-column__temperature{
        width: 60px;
    }
}

.author{
    padding: 35px 0 20px 0;
    text-align: center;
}

.author-heart{
    margin: 0 5px;
    vertical-align: text-top;
}

.author-link,
.author-link:visited{
    color: inherit;
}

